.navbar {
  border: 0;
}
.navbar-brand {
  padding-bottom: 15px;
  padding-top: 15px;
}
.navbar-brand > img {
  max-height: 50px;
}
.section {
  padding-bottom: 60px;
  padding-top: 60px;
}
.section-intro {
  background: #5e6d7b url('../img/hero-bg.jpg') center center no-repeat;
  background-size: cover;
  color: #fff;
  padding-top: 0;
}
.section-intro .navbar {
  margin-bottom: 60px;
}
.section-intro .lead {
  margin-top: 60px;
}
.section-map .page-header {
  margin-top: 90px;
}
.label {
  border-radius: 0;
  font-weight: 300;
  text-transform: uppercase;
}
.text-danger {
  color: #e70022;
}
.panel {
  border-width: 8px;
  box-shadow: none;
}
.panel-activity .panel-body {
  padding: 60px 60px 140px;
}
.mosaic {
  display: block;
  margin-bottom: -150px;
  margin-left: auto;
  margin-right: auto;
  max-width: 80%;
  position: relative;
  top: -150px;
}
.divider {
  border-top-width: 2px;
  width: 160px;
}
.footer {
  color: #00a0e8;
  padding-bottom: 60px;
  padding-top: 60px;
  text-align: center;
}
.footer h1 {
  font-size: 80px;
}
.footer strong {
  color: #6396ad;
}
